.timezone-select {
  border-radius: 2vw;
  bottom: 0.5vw;
  cursor: pointer;
  font: 1vw 'Varela Round', sans-serif;
  left: -9vw;
  padding: 0.5vw;
  position: relative;
}

.timezone-select:focus {
  outline: none;
}

.dark-mode {
  .timezone-select {
    background-color: #181a1b;
    border-color: #575757;
    color: rgb(190, 185, 175);
  }
}
